<template>
    <div class="loadmore v-loadmore" :class="{'v-loadmore_line':!showLoading, 'v-loadmore_dot': !showLoading && !tip}">
        <i class="weui-loading" v-if="showLoading"></i>
        <span class="v-loadmore__tips" v-show="tip || !showLoading">{{tip}}</span>
    </div>
</template>

<script>
export default {
    name: 'load-more',
    props: {
        showLoading: {
            type: Boolean,
            default: true
        },
        tip: String
    }
}
</script>

<style lang="less" scoped>
.loadmore {
  &.v-loadmore_line {
    display: flex;
    border-top: 0;
    align-items: center;

    &:before,
    &:after {
      //   position: relative;
      //   top: -1px;
      flex: 1;
      content: "";
      border-top: 1px solid #e5e5e5;
    }
  }
}

.v-loadmore {
  width: 65%;
  margin: 1.5em auto;
  line-height: 1.6em;
  font-size: 14px;
  text-align: center;
}
.v-loadmore__tips {
  display: inline-block;
  vertical-align: middle;
}

.v-loadmore_line {
  border-top: 1px solid #e5e5e5;
  //   margin-top: 2.4em;
  .v-loadmore__tips {
    position: relative;
    // top: -0.9em;
    padding: 0 0.55em;
    color: #999999;
  }
}
.v-loadmore_dot {
  .v-loadmore__tips {
    padding: 0 0.16em;
    &:before {
      content: " ";
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: #e5e5e5;
      display: inline-block;
      position: relative;
      vertical-align: 0;
      top: -0.16em;
    }
  }
}

.weui-loading {
  width: 20px;
  height: 20px;
  display: inline-block;
  vertical-align: middle;
  animation: weuiLoading 1s steps(12, end) infinite;
  background: transparent
    url("data:image/svg+xml;base64,")
    no-repeat;
  background-size: 100%;
  &.weui-loading_transparent {
    background-image: url("data:image/svg+xml;base64,");
  }
}

@-webkit-keyframes weuiLoading {
  0% {
    transform: rotate3d(0, 0, 1, 0deg);
  }

  100% {
    transform: rotate3d(0, 0, 1, 360deg);
  }
}

@keyframes weuiLoading {
  0% {
    transform: rotate3d(0, 0, 1, 0deg);
  }

  100% {
    transform: rotate3d(0, 0, 1, 360deg);
  }
}
</style>



