<template>
    <div class='x-cell' :class="{'border-1px':borderLine,'top-line':topLine}">
        <slot name="icon"></slot>
        <div class='title'>{{title}}</div>
        <div class='desc' :class="{'mr-l':isLink}">{{desc}}</div>
        <slot name="desc"></slot>
        <img v-if="isLink" class='link-icon' src='data:image/png;base64,' alt="">
    </div>
</template>

<script>
export default {
    props: [
        "title",
        "desc",
        "isLink",
        "borderLine",
        "topLine",
        "to",
    ]
}
</script>

<style lang="less" scoped>
.x-cell {
  position: relative;
  display: flex;
  background-color: #fff;
  padding: 12px 3%;
  box-sizing: border-box;
  overflow: hidden;
  align-items: center;

  .title {
    flex: 1.2;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    //   font-size: 16px;
  }

  .desc {
    //   font-size: 24px;
    color: #aaa;
    //   flex: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-align: right;
    /* display: table-cell;  */
    /* line-height: 30rpx;  */
    /* vertical-align:middle; */
  }

  .link-icon {
    width: 10px;
    height: 17px;
    margin-left: 3px;
  }

  .mr-l {
    margin-right: 20px;
  }
}
.border-1px {
  &::after {
    content: " ";
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    height: 1px;
    border-bottom: 1px solid #e3e5e9;
    color: #e3e5e9;
    transform-origin: 0 100%;
    transform: scaleY(0.5);
  }
}

.top-line {
  &::before {
    content: " ";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    height: 1px;
    border-top: 1px solid #e3e5e9;
    color: #e3e5e9;
    transform-origin: 0 0;
    transform: scaleY(0.5);
  }
}
</style>



